.main-body {
  padding-top: 200px;
  background-color: rgba(106, 106, 106, 0.614);
  color: white;
  font-weight: 400;
  width: 100%;
  position: absolute;
}

.first-block .base {
  background-color: rgba(255, 0, 0, 0.4);
  position: absolute;
  top: 205px;
  right: 0;
  bottom: 0;
  left: 50px;
  transform: translateZ(0);
  height: 100px;
  width: 100px;
  display: flex;
  flex-direction: row-reverse;
}
.second-block .back {
  background-color: rgba(255, 0, 0, 0.4);
  position: absolute;
  top: 255px;
  right: 0;
  bottom: 0;
  left: 100px;
  transform: translateZ(-1px) scale(2);

  height: 100px;
  width: 100px;
  z-index: -15;
}
h1{
  text-decoration: none;
  
}
.projects-part{
  margin: 50px 0 0 0;
  background-color: rgba(106, 106, 106, 0.614);
  width: 100%;
  height: 100%;
}
.html-proj{
border: 0.5px solid black;
height: 500px;
overflow-y:scroll;
display: flex;
flex-direction: row;
padding: 20px 20px 0 0;
}
.html-proj img{
  width: 50%;
  height: 85%;
  filter: blur(3px);
}
.html-proj img:hover{
  filter: blur(0px);
  cursor: pointer;
}
.html-proj .descript{
    width: 50%;
    height: 85%;
    display: flex;
    justify-content: center;
}
